import Mustache from 'mustache'

export default class Component {
  constructor(name, $container) {
    this.name = name;
    this.$container = $container;
    this.rendered = false;
    this.subTokens = {};

    console.log("A>>>>>>", this.$container);
    this.$ = this.$container.find.bind(this.$container);
    //this.$container.on("destroyed", this.destroyed.bind(this));
  }

  getData(cb, param) {
    cb();
  }

  render(data, template, cb, param) {
    this.$container.html(Mustache.render(template, data));
    cb();
  }

  loadChildren(cb, param) {
    ;
  }

  load(param, cb) {
    console.log("Component." + this.name);
    this.rendered = false;
    this.getData(
      data =>
        this.render(
          data, this.template, a =>
            this.afterRender(cb)
        )
    );
  }
}


